.header-top.container{
    display: flex;
    justify-content:space-between;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
}
.header-top__price-link{
    display: none;
}
.header-top__call-list{
    display: none;
}

@media screen and(min-width: 768px){
    .header-top.container{    
    padding-top: 24px;
    padding-bottom: 24px;
}
.header-top__price-link{
    display: block;
    @include font (18px, 700, 28px);
}
}

@media screen and(min-width: 1440px){
    .header-top.container{    
    padding-top: 20px;
    padding-bottom: 20px;
}
.header-top__price-link{
    @include font (24px, 700, 28px);
}
.header-top__call-list{
    display: block;
    @include font (24px, 500, 28px);
    font-weight: 500;
font-size: 24px;
line-height: 28px;
color: #2F4377;
& .header-top__call-item{
    margin-bottom: 8px;    
}
& > :last-child{
    margin-bottom: 0;
}
}
.contacts{
    display: none;
}
}